import React, { useState } from "react";
import MaterialTable from "material-table";
import { useHistory } from "react-router-dom";
import { CCard, CCardBody } from "@coreui/react-pro";
import EDU_AXIOS from "src/helpers/edu_axios";
import { TablePagination } from '@material-ui/core';

function ManageChainSchool() {
  let history = useHistory();
  const [state, setstate] = useState([])
  const columns = [
    {
      title: "Name",
      field: "name",
    },
    {
      title: "User Name",
      field: "username",
    },
    {
      title: "Email",
      field: "email",
    },
  ];
  let url =
  process.env.REACT_APP_API_PATH + "/auth/get-chain-schools";
  EDU_AXIOS.get(url)
  .then((resp) => {
    console.log(resp);
   setstate(resp.data.data)
  });
  return (
    <CCard>
      <CCardBody>
        <MaterialTable
          title="Chain Schools"
          // data={(query) =>
          //   new Promise((resolve, reject) => {
          //     let url =
          //       process.env.REACT_APP_API_PATH + "/auth/get-chain-schools?";
          //     url += `page= ${query.page + 1}`;
          //     url += `&limit= ${query.pageSize}`;
          //     EDU_AXIOS.get(url)
          //       .then((resp) => {
          //         resolve({
          //           data: resp.data.data, // your data array
          //           page: query.page, // current page number
          //           totalCount: resp.data.total_count, // total row number
          //         });
          //       })
          //       .catch((error) => {
          //         console.log(error);
          //       });
          //   })
          // }
          data={state}
          columns={columns}
          onSelectionChange={(rows) => setSelectedRows(rows)}
          options={{
            search: true,
            paging: true,
            filtering: false,
            exportButton: true,
            actionsColumnIndex: 4,
            rowStyle: (x) => {
              if (x.tableData.id % 2) {
                return { backgroundColor: "#f9f9f9" };
              }
            },
            pageSize:20,       
              emptyRowsWhenPaging: true, 
              pageSizeOptions:[20,40,60]
          }}
          
          actions={[
            {
              icon: "visibility",
              tooltip: "View School",
              onClick: (event, rowData) =>
                history.push(`chain-school/${rowData.id}`),
            },
          ]}
        />
      </CCardBody>
    </CCard>
  );
}

export default ManageChainSchool;
